button.query-api {
  display: inline-block;
  padding: 0.5em 3em;
  border: 0.16em solid #FFFFFF;
  margin: 0 0.3em 0.3em 0;
  box-sizing: border-box;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'Cormorant SC', serif;
  font-weight: 400;
  color: #222222;
  text-align: center;
  transition: all 0.15s;
  font-size: 1.2em;
}

button.query-api:hover {
  color: #666666;
  border-color: #DDDDDD;
}

button.query-api:active {
  color: #BBBBBB;
  border-color: #BBBBBB;
}
.domain-container {
  margin-right: 2%;
  float: left; }

.domain-container.drill-down-open {
  width: 50%; }

.domain-container.drill-down-closed {
  width: 100%; }

.api-button button {
  width: 100%; }

.drill-down {
  width: 45%;
  float: left; }

ul {
  list-style-type: none; }

ul.in-table-list {
  margin: 0;
  padding: 0; }

#domain-selector button {
  display: inline-block;
  padding: 0.35em 1.2em;
  border: 0.1em solid #FFFFFF;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.15em;
  box-sizing: border-box;
  font-weight: 500;
  color: #2d3a83;
  text-align: center;
  transition: all 0.2s;
  text-decoration: none; }

#domain-selector button:not(.viewing):hover {
  color: #000000;
  background-color: #FFFFFF; }

.unclickable {
  cursor: default; }

.clickable:hover {
  font-weight: bold;
  cursor: pointer; }

.table-title {
  font-family: 'Cormorant SC', serif;
  font-size: 40px; }

.got-app {
  background-color: #969696; }


div.title {
  border-bottom: thick double #000000;

}

div.drill-down {
  background-color: #DDDDDD;
  font-family: 'Cormorant SC', serif;
  margin-top: 5em;
  font-size: 1.5em;
}

.drill-down-back {
  float: left;
}

.close-drill-down {
  float: right;
}

.material-icons.drill-down-back {
  color: green;
}

.material-icons.close-drill-down{
  color: red;
}

